.preloaderWrapper {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.preloaderWrapper img {
    max-width: 100%;
    max-height: 100%;
}

@media (max-height: 490px) {
    .preloaderWrapper img {
        max-width: 50%;
        max-height: 50%;
    }
}

@media (max-height: 320px) {
    .preloaderWrapper img {
        max-width: 25%;
        max-height: 25%;
    }
}